.game{
    margin: auto;
    width:90%;
    font-family: Arial, Helvetica, sans-serif;
}
.game-title{
    padding:2px 8px;
    background-color: #333;
    color:white;
    display: flex;
    align-content: center;
    margin-bottom: 5px;
    border-radius: 7px;
}
.game-title h4{
    font-size: 15px;
    display:flex;
    align-items: center;
    margin:auto 0 ;
    padding:6px;
}
.game-body{
    padding:4px 6px;
    display: flex;
    align-items:center;
}
.game .time{
    margin-right: 7px;
    font-weight: 500;
}
.game .playing{
    margin-right:4px;
    font-weight:500;
    color:gray;
    padding:3px;
}
.game .score{
    cursor:pointer;
}
.time-red{
    color:red;
}
.score-red{
    color:red;
}