.footer{
  
    background: #333;
    color:#fff;

}
.link-container{
    padding:10px 12px;
    display:grid;
    grid-template-columns: repeat(4 , 1fr);
}

.links{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.links a{
    color:white;

}
.links a:hover{
    color:blue;
}
.link{
    padding:3px 12px;
    margin:5px auto;
    border-bottom: 1px solid  rgba(0,100,100,0.5);
}
.link:hover{
    background-color: black;
    border-radius: 5px;
   
}
.links .link .MuiSvgIcon-root{
    margin-right: 5px;
}

.contain-this{
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    margin:auto;
}
.reach-out{
    padding:5px 10px;
    margin-right:10px;
    flex:1;
}
.form-element input{
    border:none;
    outline:none;
    background:transparent;
    width:100%;
    padding:3px;
}
.form-element{
    background-color: #fff;
    padding:10px 25px;
    border-radius: 999px;
    display: flex;
    flex:0.5;
    justify-content: center;
    align-items: center;
    
}
form{
    text-align: center;
}
.formm{
    display:flex;
    justify-content: center;
    align-items: center;
    width:100%;
    /* flex-direction: column; */
   
}
form h4{
    margin:10px 0  8px 0;
    font-weight: 300;
    font-family: Arial, Helvetica, sans-serif;
}
.social-media{
    flex:0.5;
    padding:10px 15px;
    display:flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.form-element .MuiSvgIcon-root{
    color:red;
    margin-right: 3px;
}
.formm button{
    padding:7px 15px;
    color:white;
    margin-left: 10px;
    background-color: rgba(0,300,0,0.5);
    outline:none;
}
.formm button:hover{
    background-color: rgba(0,150,0,0.5);
}

.facebook{
    color:rgba(0,0,500,0.7);
    background-color: #fff;   
}
.media{
    display:flex;
    padding:5px 10px;
    justify-content: space-between;
    align-items: center;
}
.media .MuiSvgIcon-root{
    margin-right: 10px;
}
.instagram{
    color:rgba(300 ,0,0,0.5);
    background:#fff;
}
.whatsapp{
    color:#fff;
    background-color: green;
}
.twitter{
    background:rgba(0,0,1000,0.8);
}
.footer-info{
    padding:5px 10px;
    background-color: black;
  margin-top: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width:590px){
    .link-container{
        grid-template-columns: repeat(2 , 1fr);
    }
       .links{
           margin-bottom:10px;
       } 
      
}
@media(max-width:900px){
    .contain-this{
        display:block;
    }
    .form-element{
        width:1200px;
    }
}